<template>
  <div class="container w_con">
    <div class="container container1" >
      <h1 class="text">Introduction</h1>
      <div class="text">Staking Engine is a stand-alone solution to implement staking. An extended set of settings allows to customise staking exactly for any needs.
        This guide will briefly show how to set up Staking Engine.</div>
      <h2 class="text" style="margin-top: 20px;">What is Staking</h2>
      <div class="text">Locking up an NFT in a Defi platform to get an utility token in return.</div>
      <h2 class="text" style="margin-top: 20px;">What is Utility Token</h2>
      <div class="text">A custom token that is used as an internal currency, it is commonly used to interact with NFTs,
        for example to breed, change attributes as name or as a vending machine ticket to airdrop additional NFTs,
        play a mini-game or as a hard currency in gamified projects.
        In addition it creates an inner economy with established price.
        Creating a liquidity pool or listing on an exchange will encourage participants to invest into your project.</div>
      <h2 class="text" style="margin-top: 20px;">Why Do you need Staking</h2>
      <div class="text">Staking solves three annoying problems:</div>
      <li class="text">
        Acts as an intermediate between non-fungible NFTs and fungible custom token, it is one of the most practical way to grant your holder a utility token.
      </li>
      <li class="text">
        It makes holders not to do not throw their NFTs on the stock exchange right after the purchase,
        but keep them out for a long time.
        That would decrease demand rising a price and will make it easier to release new NFTs to the same collection.
      </li>
      <li class="text">
        Holders consider NFT as an investment, which encourage them to buy more as long as they can get staking dividends.
      </li>
