<template>
  <button class="next light rounded-3" v-on:click="direct()" style="margin-top: 50px;">
    <b>{{text}}</b>
    <span >&#x27A4;</span>
  </button>
</template>

<script>
import {PagesMap} from "@/env";
export default {
  name: "NextButton",
  props: ['setPage', 'text', 'next'],
  methods:{
    direct(){
      window.location.href = PagesMap.get(this.next);
    }
  }
}
</script>
