<template>
  <div class="container w_con">
    <div class="container container1" >
      <h1 class="text">Instance</h1>
      <div class="text"><b>Instance</b> stores presets for each individual staking, and own treasury accounts for rewards,</div>
      <h2 class="text" style="margin-top: 20px;">Configure</h2>
      <div class="text">Move to <b>Instance</b> tab on navbar and click Configure</div>
      <img src="@/assets/instance_configure.png" class="img_ful"/>
      <li class="text">
        <b class="light">Collection</b>: put your collection address, if you don’t have it - put any NFT mint address
      </li>
      <li class="text">
        <b class="light">Specification</b>: Engine supports two variants of staking:
        <b>Stake</b> is a standard, holders get <b>unit reward</b> for every unit time of staking, can unstake or claim intermediate rewards at any time.
        <b>Lock</b> provides a single reward for a unit time and holders can not unstake until the end of period.
      </li>
      <li class="text">
        <b class="light">Unit Time</b> is a period for reward accrual.
      </li>
      <li class="text">
        <b class="light">Reward Tokens</b>: award your holders with combination of <b>Utility Tokens</b>, <b>Solana</b> or
        <b>Stable Coins</b>, Engine supports up to 5 tokens at the same time. For an utility token put mint address.
      </li>
      <h2 class="text" style="margin-top: 20px;">Deploy</h2>
      <div class="text">Click Configure and check setting before deploying</div>
      <div class="warning rounded-2">
        You want be able to reset Specification or Reward Token list after you deploy or add new NFT’s mints
      </div>
      <img src="@/assets/instance_configured.png" class="img_ful"/>
      <h2 class="text" style="margin-top: 10px;">States</h2>
      <div class="text">After deploying an <b>Instance</b> you would recieve NFT that represents current state</div>
      <div style="display: flex">
        <div style="display: inline">
          <div style="display: flex">
            <img :src="IdleInitImg" class="square"/>
            <div class="text" style="max-width: 256px;">
              <b class="light">Init</b>: you can make any changes, but holders can not stake yet
            </div>
          </div>
          <div style="display: flex">
            <img :src="IdleRunningImg" class="square"/>
            <div class="text" style="max-width: 256px;">
              <b class="light">Running</b>: holders can stake and unstake, but you cannot make any changes
            </div>
          </div>
          <div style="display: flex">
            <img :src="IdlePausedImg" class="square"/>
            <div class="text" style="max-width: 256px;">
              <b class="light">Pause</b>: you can make change until time and reward values, but holders cannot stake until you launch it again, however anyone can unstake their NFTs anytime
            </div>
          </div>
        </div>
        <img src="@/assets/instance_phantom.png" class="rectangle-side"/>
      </div>
      <img src="@/assets/instance_phantom.png" class="rectangle-bottom"/>
      <h2 class="text" style="margin-top: 30px;">Retrieve</h2>
      <div class="text">If you have lost your repository you can retrieve <b>Instance</b> from <b>Instance Engine NFT</b>,
        at the start in Instance tab you would see very <b>Instances</b> owned by your account, choose one to load.</div>
      <img src="@/assets/instance_load.png" class="img_ful"/>
      <div class="warning rounded-2" style="margin-top: 0;">
        You would restore full access only if you have already launched Instance
      </div>
      <h2 class="text" style="margin-top: 30px;">Update Time</h2>
      <div class="text">After very time you launch the <b>Instance</b> the <b>update time</b> would be update
        Engine offers honest rules for both NFT holder and project owner.</div>
      <div class="warning rounded-2">
        Rewards for staking calculates as the sum of rewards based on <b>unit time</b> and <b>values</b> before
        last update time and rewards based on <b>new values</b> for remaining time, in other words,
        changes would be applied for periods after <b>update time</b>.
      </div>
      <NextButton :set-page="setPage" text="Standard" next="StakeStandard"/>
    </div>
  </div>
</template>

<script>
import NextButton from "@/components/NextButton";
import {IdleInitImg, IdlePausedImg, IdleRunningImg} from "@/env";


export default {
  name: "StakeInstance",
  components: {NextButton},
  props: ['setPage'],
  data(){
    return{
      IdleInitImg: IdleInitImg,
      IdleRunningImg: IdleRunningImg,
      IdlePausedImg: IdlePausedImg
    }
  },
}
</script>

<style scoped>
.square{
  max-width: 250px;
  max-height: 250px;
  width: 30vw;
  height: 30vw;
}
.rectangle-side{
  max-width: 275px;
  max-height: 400px;
  width: calc(calc(100vw - 750px) * 1000);
  height: 52vw;
}
.rectangle-bottom{
  max-width: 275px;
  max-height: 400px;
  width: calc(calc(750px - 100vw) * 1000);
  height: calc(calc(750px - 100vw) * 1000);
  margin: 10px 0 0 calc(calc(90vw - 275px) / 2);
}
</style>