import {createRouter, createWebHistory} from "vue-router";
import StakeClusters from "./components/StakeEngine/StakeClusters.vue"
import StakeDeposits from "./components/StakeEngine/StakeDeposits.vue"
import StakeGetStarted from "./components/StakeEngine/StakeGetStarted.vue"
import StakeInstance from "./components/StakeEngine/StakeInstance.vue"
import StakeIntroduction from "./components/StakeEngine/StakeIntroduction.vue"
import StakeLog from "./components/StakeEngine/StakeLog.vue"
import StakeRewards from "./components/StakeEngine/StakeRewards.vue"
import StakeStandard from "./components/StakeEngine/StakeStandard.vue"
import StakeVerifyUpload from "./components/StakeEngine/StakeVerifyUpload.vue"
import StakeVirtualMachine from "./components/StakeEngine/StakeVirtualMachine.vue"
import UICustom from "./components/StakeUI/UICustom.vue"
import UISetUp from "./components/StakeUI/UISetUp.vue"
import GreetingPage from "./components/GreetingPage.vue"
import SecurityPage from "./components/SecurityPage.vue"


export default createRouter({
    history: createWebHistory(''),
    routes:[
        {path: "/", component: GreetingPage},
        {path: "/security", component: SecurityPage},
        {path: "/stake-engine/introduction", component: StakeIntroduction},
        {path: "/stake-engine/getting-started", component: StakeGetStarted},
        {path: "/stake-engine/instance", component: StakeInstance},
        {path: "/stake-engine/standard", component: StakeStandard},
        {path: "/stake-engine/rewards", component: StakeRewards},
        {path: "/stake-engine/virtual-machine", component: StakeVirtualMachine},
        {path: "/stake-engine/logs", component: StakeLog},
        {path: "/stake-engine/verify-upload", component: StakeVerifyUpload},
        {path: "/stake-engine/deposits", component: StakeDeposits},
        {path: "/stake-engine/clusters", component: StakeClusters},
        {path: "/stake-engine/ui/set-up", component: UISetUp},
        {path: "/stake-engine/ui/custom", component: UICustom},
    ]
});