    <h6 :class="activePage===next? 'light' : 'title'">{{text}}</h6>
    <span :class="activePage===next? 'light' : 'title'" style="right: 10px; margin-right: auto;">&#10095;</span>
  </button>
</template>

<script>
export default {
  name: "NavRefButton",
  props: ['setPage', 'activePage', 'text', 'next', 'isTitle']
}
</script>

<style>
.btn{
  background-color: transparent;
  border: none;
  padding: 0;
}
</style>
