<template>
  <GreetingPage v-if="pageName==='GreetingPage'" :set-page="setActivePage" :direct="directToPage"/>
  <d v-else>
    <div class="line page_back left_bar" style="border-left: none; border-bottom: none; border-top: none;" id ="sidebar">
      <div class="nav-item hs" style="display: grid; grid-auto-flow: column; border-left: none; border-right: none; border-top: none;">
        <h4 class="text company_name" ><b>Zephyr Studios</b></h4>
        <a class="side_cross" onclick="document.getElementById('sidebar').style.width = '0'">&times;</a>
      </div>
      <NavBar :page-name="pageName" :set-page-name="directToPage"/>
    </div>
    <div class="navbar-expand page_back line" style="border-left: none; border-right: none; border-top: none; position: fixed; z-index: 1;">
      <span class="side_open" onclick="document.getElementById('sidebar').style.width = '250px'">&#9776;</span>
      <div class="nav-item" style="display: grid; grid-auto-flow: column;">
        <img src="@/assets/company_logo.png" class="company_logo"/>
        <h4 class="text company_name" v-on:click="directToPage('GreetingPage')"><b>Zephyr Studios</b></h4>
      </div>
      <div class="nav-item" style="">
        <a  :href="refToDiscord" style="text-decoration: none;">
          <img src="@/assets/discord_logo.png" class="social"/>
        </a>
        <a :href="refToTwitter" style="text-decoration: none;">
          <img src="@/assets/twitter_logo.png" class="social"/>
        </a>
      </div>
    </div>
    <div style="display: flex; margin-top: min(15vw, 90px); position: absolute;">
      <div class="page_back side_bar line" style="border-left: none; border-bottom: none; border-top: none; position: fixed;">
        <NavBar :page-name="pageName" :set-page-name="directToPage"/>
      </div>
      <d class="page_back side_bar"/>
      <router-view :set-page="setActivePage"></router-view>
    </div>
  </d>
</template>
<script>


import NavBar from "@/components/NavBar";
import {OfficialDiscord, OfficialTwitter, PagesMap, RoutesMap} from "@/env";
import {useLocalStorage} from '@vueuse/core';
import GreetingPage from "@/components/GreetingPage";
export default {
  name: 'App',
  components: {
    GreetingPage,
    NavBar
  },
  methods: {
    setActivePage(name){
      this.pageName = name;
    },
    directToPage(name){
      this.pageName = name;
      window.location.href = PagesMap.get(name);
    }
  },
  data(){
    return{
      pageName: useLocalStorage("activeIndex", "GreetingPage"),
      refToDiscord: OfficialDiscord,
      refToTwitter: OfficialTwitter
    }
  },
  mounted() {
    const path = window.location.pathname;
    const name = RoutesMap.get(path);
    if(!name){
      window.location.href = "/";
      return;
    }
    this.pageName = name;
  }
}
</script>

<style>
@import "./styles.css";
</style>
