<template>
  <div class="container w_con">
    <div class="container container1" >
      <h1 class="text">Getting Started</h1>
      <h2 class="text" style="margin-top: 10px;">Tool required</h2>
      <li class="text">
        <b class="light">Git</b> is required to clone the repository, any version is suitable.
        Download from: <a class="ref" :href="InstallGit">{{InstallGit}}</a>
        or via <b>Brew</b> on Mac Os or Linux
      </li>
      <li class="text">
        <b class="light">Node</b> is required to run java script code, use any stable version from v.17.
        Download from <a class="ref" :href="InstallNode">{{InstallNode}}</a>
        or via <b>Brew</b> on Mac Os or Linux
      </li>
      <h2 class="text" style="margin-top: 20px;">Clone and Install</h2>
      <div class="text">
        Engine code would be run on your local machine, clone repository and install node_modules to launch. Run in your working folder:
      </div>
      <div class="cmd rounded-2">
        <b class="orange">git</b> clone <d class="ref">{{GitEngineRep}}</d>
      </div>
      <div class="text">Move to dashboard:</div>
      <div class="cmd rounded-2">
        <b class="orange">cd</b> stake_engine/engine_ui
      </div>
      <div class="text">Install required modules with npm packet manager:</div>
      <div class="cmd rounded-2">
        <b class="orange">npm</b> run download
      </div>
      <div class="text">wait till process ends.</div>
      <h2 class="text" style="margin-top: 20px;">Run</h2>
      <li class="text">
        Before first launch put <b class="light">hash list</b> into <b>hashes.json</b> and put it into <b>./stake_engine/engine_ui</b> folder
      </li>
      <div class="text" style="margin-top: 10px;">
        To fund and sign transactions you need one of these virtual wallets installed as your browser extention
      </div>
      <li class="text" style="margin-top: 10px;">
        <b style="color: #543CC8">Phantom</b>: <a class="ref" href="https://phantom.app/">https://phantom.app/</a>
      </li>
      <li class="text">
        <b style="color: #6E66FA">Slope</b>: <a class="ref" href="https://slope.finance/">https://slope.finance/</a>
      </li>
      <li class="text">
        <b style="color: #FD8A1A">Solfare</b>: <a class="ref" href="https://solflare.com/">https://solflare.com/</a>
      </li>
      <div class="text" style="margin-top: 10px;">Start dashboard:</div>
      <div class="cmd rounded-2">
        <b class="orange">npm</b> run serve
      </div>
      <div class="text">Open <a class="ref" href="http://localhost:300/">http://localhost:3000/</a> in your browser if not redirected automatically</div>
      <NextButton :set-page="setPage" text="Instance" next="StakeInstance"/>
    </div>
  </div>
</template>

<script>
import NextButton from "@/components/NextButton";
import {GitEngineRep, InstallGit, InstallNode} from "@/env";
export default {
  name: "StakeGetStarted",
  components: {NextButton},
  props: ['setPage'],
  data(){
    return{
      InstallGit: InstallGit,
      InstallNode: InstallNode,
      GitEngineRep: GitEngineRep
    }
  },
}
</script>
