<template>
  <div class="container w_con">
    <div class="container container1" >
      <h1 class="text">Standard</h1>
      <h2 class="text" style="margin-top: 10px;">Metaplex V2</h2>
      <div class="text">In Solana ecosystem conventional standard is introduced by metaplex foundation:
        <a  :href="MetaplexRef" class="ref">{{MetaplexRef}}</a>, it shares the same idea with <b>ERC20</b> and <b>ERC721</b> in <b>ETH</b>, providing
        <b>Metadata</b> and <b>Collection</b> accounts common for NFT recognition by subsystems as wallets and marketplaces.
        Engine is supposed to work with it and reveals the potential only with the latest <b>Metaplex V2</b> Metadata standard:
        <a  :href="MetaplexTokenRef" class="ref">{{ MetaplexTokenRef }}</a>.
        If you are using <b>Magic Eden</b> or <b>Candy Machine V2</b> for minting, don’t worry, all of them using latest versions.</div>
      <h2 class="text" style="margin-top: 20px;">Hashes</h2>
      <div class="text">Every NFT in Solana is represneted as signle token, in other words every NFT hash it’s own mint(hash). Look for mint address on Solscan: </div>
      <img src="@/assets/solscan_mint.png" class="img_mint"/>
      <h2 class="text" style="margin-top: 10px;">Hash List</h2>
      <div class="text"><b class="light">Hash List</b> is a complete list of all mints from your collection.
        Engine requires it to strictly identify witch NFT belongs to staking.
        If you are using Candy Machine on mainnet you can retrieve hash list from:
        <a  :href="MagicEdenMintToolRef" class="ref">{{ MagicEdenMintToolRef }}</a>,
        otherwise try from Collection address with our free code:
        <a  :href="GitDemoRep" class="ref">{{ GitDemoRep }}</a></div>
      <h2 class="text" style="margin-top: 20px;">Collection</h2>
      <div class="text"><b>Collection</b> account represents your collection ownership, all your NFTs would keep reference to it and must be <b>verified</b>.
        That is an indicator for marketplaces that NFTs are from the same collection and made by the same author. Look for collection on <a href="https://solscan.io/" class="ref">Solscan:</a></div>
      <img src="@/assets/solscan_collection.png" class="img_collection"/>
      <NextButton :set-page="setPage" text="Rewards" next="StakeRewards"/>
    </div>
  </div>
</template>

<script>
import NextButton from "@/components/NextButton";
import {MagicEdenMintToolRef, MetaplexRef, MetaplexTokenRef, GitDemoRep} from "@/env";
export default {
  name: "StakeStandard",
  components: {NextButton},
  props: ['setPage'],
  data(){
    return{
      MetaplexRef: MetaplexRef,
      MetaplexTokenRef: MetaplexTokenRef,
      MagicEdenMintToolRef: MagicEdenMintToolRef,
      GitDemoRep: GitDemoRep,
    }
  },
}
</script>

